"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { zodResolver } from "@hookform/resolvers/zod"
import { useForm } from "react-hook-form"
import { z } from "zod"
import { Plus, Trash2, Save, ArrowLeft, X, Edit, Copy, AlertCircle } from "lucide-react"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"
import { useToast } from "@/components/hooks/use-toast"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog"

// Define the variation option schema
const VariationOptionSchema = z.object({
  id: z.string().optional(),
  value: z.string().min(1, "Option value is required"),
  description: z.string().optional(),
})

// Define the variation schema
const VariationSchema = z.object({
  id: z.string().optional(),
  name: z.string().min(1, "Variation name is required"),
  description: z.string().optional(),
  options: z.array(VariationOptionSchema).min(1, "At least one option is required"),
})

// Define the variation set schema
const VariationSetSchema = z.object({
  id: z.string().optional(),
  name: z.string().min(1, "Set name is required"),
  description: z.string().optional(),
  variations: z.array(VariationSchema).min(1, "At least one variation is required"),
})

type VariationOption = z.infer<typeof VariationOptionSchema>
type Variation = z.infer<typeof VariationSchema>
type VariationSet = z.infer<typeof VariationSetSchema>

// Mock data for variation sets
const mockVariationSets: VariationSet[] = [
  {
    id: "1",
    name: "Clothing Sizes",
    description: "Standard clothing sizes",
    variations: [
      {
        id: "1-1",
        name: "Size",
        description: "Clothing size",
        options: [
          { id: "1-1-1", value: "Small", description: "Small size" },
          { id: "1-1-2", value: "Medium", description: "Medium size" },
          { id: "1-1-3", value: "Large", description: "Large size" },
          { id: "1-1-4", value: "X-Large", description: "Extra large size" },
        ],
      },
    ],
  },
  {
    id: "2",
    name: "Furniture Colors and Materials",
    description: "Common furniture variations",
    variations: [
      {
        id: "2-1",
        name: "Color",
        description: "Furniture color",
        options: [
          { id: "2-1-1", value: "Black", description: "Black finish" },
          { id: "2-1-2", value: "White", description: "White finish" },
          { id: "2-1-3", value: "Natural", description: "Natural wood finish" },
        ],
      },
      {
        id: "2-2",
        name: "Material",
        description: "Furniture material",
        options: [
          { id: "2-2-1", value: "Wood", description: "Solid wood" },
          { id: "2-2-2", value: "Metal", description: "Metal frame" },
          { id: "2-2-3", value: "Glass", description: "Glass components" },
        ],
      },
    ],
  },
  {
    id: "3",
    name: "Electronics Configuration",
    description: "Common electronics variations",
    variations: [
      {
        id: "3-1",
        name: "Storage",
        description: "Storage capacity",
        options: [
          { id: "3-1-1", value: "128GB", description: "128GB storage" },
          { id: "3-1-2", value: "256GB", description: "256GB storage" },
          { id: "3-1-3", value: "512GB", description: "512GB storage" },
          { id: "3-1-4", value: "1TB", description: "1TB storage" },
        ],
      },
      {
        id: "3-2",
        name: "Color",
        description: "Device color",
        options: [
          { id: "3-2-1", value: "Black", description: "Black color" },
          { id: "3-2-2", value: "Silver", description: "Silver color" },
          { id: "3-2-3", value: "Gold", description: "Gold color" },
        ],
      },
    ],
  },
]

export default function VariationsPage() {
  const router = useRouter()
  const { toast } = useToast()
  const [variationSets, setVariationSets] = useState<VariationSet[]>(mockVariationSets)
  const [isCreating, setIsCreating] = useState(false)
  const [isEditing, setIsEditing] = useState(false)
  const [currentSetId, setCurrentSetId] = useState<string | null>(null)
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false)
  const [setToDelete, setSetToDelete] = useState<string | null>(null)
  const [searchTerm, setSearchTerm] = useState("")

  // Common variation types for quick selection
  const commonVariationTypes = ["Size", "Color", "Material", "Style", "Weight", "Capacity", "Flavor", "Scent"]

  // Form for creating/editing variation sets
  const form = useForm<VariationSet>({
    resolver: zodResolver(VariationSetSchema),
    defaultValues: {
      name: "",
      description: "",
      variations: [
        {
          name: "",
          description: "",
          options: [{ value: "", description: "" }],
        },
      ],
    },
  })

  // Reset form when switching between create and edit modes
  useEffect(() => {
    if (isCreating) {
      form.reset({
        name: "",
        description: "",
        variations: [
          {
            name: "",
            description: "",
            options: [{ value: "", description: "" }],
          },
        ],
      })
    } else if (isEditing && currentSetId) {
      const setToEdit = variationSets.find((set) => set.id === currentSetId)
      if (setToEdit) {
        form.reset(setToEdit)
      }
    }
  }, [isCreating, isEditing, currentSetId, form, variationSets])

  // Filter variation sets based on search term
  const filteredVariationSets = variationSets.filter(
    (set) =>
      set.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      set.description?.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  // Form submission handler
  const onSubmit = (data: VariationSet) => {
    try {
      if (isCreating) {
        // Generate a new ID for the set
        const newId = (Math.max(0, ...variationSets.map((set) => Number.parseInt(set.id || "0"))) + 1).toString()

        // Generate IDs for variations and options
        const newVariations = data.variations.map((variation, vIndex) => ({
          ...variation,
          id: `${newId}-${vIndex + 1}`,
          options: variation.options.map((option, oIndex) => ({
            ...option,
            id: `${newId}-${vIndex + 1}-${oIndex + 1}`,
          })),
        }))

        const newSet = {
          ...data,
          id: newId,
          variations: newVariations,
        }

        setVariationSets([...variationSets, newSet])
        toast({
          title: "Variation set created",
          description: `${data.name} has been created successfully.`,
          variant: "success",
        })
      } else if (isEditing && currentSetId) {
        // Update existing set
        setVariationSets(variationSets.map((set) => (set.id === currentSetId ? { ...data, id: currentSetId } : set)))
        toast({
          title: "Variation set updated",
          description: `${data.name} has been updated successfully.`,
          variant: "success",
        })
      }

      // Reset state
      setIsCreating(false)
      setIsEditing(false)
      setCurrentSetId(null)
    } catch (error) {
      console.error("Error saving variation set:", error)
      toast({
        title: "Error",
        description: "Failed to save variation set. Please try again.",
        variant: "destructive",
      })
    }
  }

  // Delete a variation set
  const handleDeleteSet = (id: string) => {
    setSetToDelete(id)
    setDeleteDialogOpen(true)
  }

  const confirmDeleteSet = () => {
    if (setToDelete) {
      setVariationSets(variationSets.filter((set) => set.id !== setToDelete))
      toast({
        title: "Variation set deleted",
        description: "The variation set has been deleted successfully.",
        variant: "success",
      })
      setDeleteDialogOpen(false)
      setSetToDelete(null)
    }
  }

  // Duplicate a variation set
  const handleDuplicateSet = (id: string) => {
    const setToDuplicate = variationSets.find((set) => set.id === id)
    if (setToDuplicate) {
      // Generate a new ID for the set
      const newId = (Math.max(0, ...variationSets.map((set) => Number.parseInt(set.id || "0"))) + 1).toString()

      // Generate IDs for variations and options
      const newVariations = setToDuplicate.variations.map((variation, vIndex) => ({
        ...variation,
        id: `${newId}-${vIndex + 1}`,
        options: variation.options.map((option, oIndex) => ({
          ...option,
          id: `${newId}-${vIndex + 1}-${oIndex + 1}`,
        })),
      }))

      const newSet = {
        ...setToDuplicate,
        id: newId,
        name: `${setToDuplicate.name} (Copy)`,
        variations: newVariations,
      }

      setVariationSets([...variationSets, newSet])
      toast({
        title: "Variation set duplicated",
        description: `${newSet.name} has been created successfully.`,
        variant: "success",
      })
    }
  }

  // Edit a variation set
  const handleEditSet = (id: string) => {
    setCurrentSetId(id)
    setIsEditing(true)
    setIsCreating(false)
  }

  // Variation form handlers
  const addVariation = () => {
    const variations = form.getValues("variations") || []
    form.setValue("variations", [
      ...variations,
      {
        name: "",
        description: "",
        options: [{ value: "", description: "" }],
      },
    ])
  }

  const removeVariation = (index: number) => {
    const variations = form.getValues("variations") || []
    if (variations.length > 1) {
      form.setValue(
        "variations",
        variations.filter((_, i) => i !== index),
      )
    }
  }

  const addVariationOption = (variationIndex: number) => {
    const variations = form.getValues("variations") || []
    const updatedVariations = [...variations]

    if (updatedVariations[variationIndex]) {
      updatedVariations[variationIndex].options.push({
        value: "",
        description: "",
      })

      form.setValue("variations", updatedVariations)
    }
  }

  const removeVariationOption = (variationIndex: number, optionIndex: number) => {
    const variations = form.getValues("variations") || []
    const updatedVariations = [...variations]

    if (updatedVariations[variationIndex] && updatedVariations[variationIndex].options.length > 1) {
      updatedVariations[variationIndex].options = updatedVariations[variationIndex].options.filter(
        (_, i) => i !== optionIndex,
      )

      form.setValue("variations", updatedVariations)
    }
  }

  const selectVariationType = (type: string, index: number) => {
    const variations = form.getValues("variations") || []
    const updatedVariations = [...variations]

    if (updatedVariations[index]) {
      updatedVariations[index].name = type
      form.setValue("variations", updatedVariations)
    }
  }

  return (
    <div className="p-6 md:p-8">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center gap-4">
          <Button variant="outline" size="icon" onClick={() => router.back()}>
            <ArrowLeft className="h-4 w-4" />
          </Button>
          <h1 className="text-2xl font-bold">Product Variations</h1>
        </div>

        {!isCreating && !isEditing && (
          <Button onClick={() => setIsCreating(true)}>
            <Plus className="mr-2 h-4 w-4" />
            Create Variation Set
          </Button>
        )}
      </div>

      {/* Search bar */}
      {!isCreating && !isEditing && (
        <div className="mb-6">
          <Input
            placeholder="Search variation sets..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="max-w-md"
          />
        </div>
      )}

      {/* Create/Edit Form */}
      {(isCreating || isEditing) && (
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
            <Card>
              <CardHeader>
                <CardTitle>{isCreating ? "Create Variation Set" : "Edit Variation Set"}</CardTitle>
                <CardDescription>
                  {isCreating
                    ? "Create a new set of variations that can be applied to multiple products."
                    : "Edit this variation set."}
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Set Name</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., Clothing Sizes, Color Options" {...field} />
                      </FormControl>
                      <FormDescription>A descriptive name for this set of variations.</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="description"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Description (Optional)</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., Standard clothing sizes for t-shirts" {...field} />
                      </FormControl>
                      <FormDescription>A brief description of this variation set.</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <Separator className="my-6" />

                <div className="space-y-6">
                  <h3 className="text-lg font-medium">Variations</h3>

                  {form.watch("variations").map((variation, variationIndex) => (
                    <Card key={variationIndex} className="border border-border">
                      <CardHeader className="pb-3">
                        <div className="flex items-center justify-between">
                          <CardTitle className="text-base">Variation {variationIndex + 1}</CardTitle>
                          {form.watch("variations").length > 1 && (
                            <Button
                              type="button"
                              variant="outline"
                              size="sm"
                              onClick={() => removeVariation(variationIndex)}
                              className="h-8 px-2 text-destructive hover:bg-destructive/10 hover:text-destructive"
                            >
                              <X className="h-4 w-4 mr-1" />
                              Remove
                            </Button>
                          )}
                        </div>
                      </CardHeader>
                      <CardContent className="space-y-4">
                        <div className="flex flex-wrap gap-2 mb-2">
                          {commonVariationTypes.map((type) => (
                            <Badge
                              key={type}
                              variant="outline"
                              className="cursor-pointer hover:bg-accent"
                              onClick={() => selectVariationType(type, variationIndex)}
                            >
                              {type}
                            </Badge>
                          ))}
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                          <FormField
                            control={form.control}
                            name={`variations.${variationIndex}.name`}
                            render={({ field }) => (
                              <FormItem>
                                <FormLabel>Variation Name</FormLabel>
                                <FormControl>
                                  <Input placeholder="e.g., Size, Color, Material" {...field} />
                                </FormControl>
                                <FormMessage />
                              </FormItem>
                            )}
                          />

                          <FormField
                            control={form.control}
                            name={`variations.${variationIndex}.description`}
                            render={({ field }) => (
                              <FormItem>
                                <FormLabel>Description (Optional)</FormLabel>
                                <FormControl>
                                  <Input placeholder="e.g., Available sizes" {...field} />
                                </FormControl>
                                <FormMessage />
                              </FormItem>
                            )}
                          />
                        </div>

                        <div className="space-y-3 mt-4">
                          <h4 className="text-sm font-medium">Options</h4>

                          {variation.options.map((_, optionIndex) => (
                            <div key={optionIndex} className="grid grid-cols-1 md:grid-cols-2 gap-3 items-end">
                              <FormField
                                control={form.control}
                                name={`variations.${variationIndex}.options.${optionIndex}.value`}
                                render={({ field }) => (
                                  <FormItem>
                                    <FormLabel className={optionIndex !== 0 ? "sr-only" : undefined}>
                                      Option Value
                                    </FormLabel>
                                    <FormControl>
                                      <Input placeholder={`Enter ${variation.name || "option"} value`} {...field} />
                                    </FormControl>
                                    <FormMessage />
                                  </FormItem>
                                )}
                              />

                              <div className="flex gap-2">
                                <FormField
                                  control={form.control}
                                  name={`variations.${variationIndex}.options.${optionIndex}.description`}
                                  render={({ field }) => (
                                    <FormItem className="flex-1">
                                      <FormLabel className={optionIndex !== 0 ? "sr-only" : undefined}>
                                        Description (Optional)
                                      </FormLabel>
                                      <FormControl>
                                        <Input placeholder="Optional description" {...field} />
                                      </FormControl>
                                      <FormMessage />
                                    </FormItem>
                                  )}
                                />

                                <Button
                                  type="button"
                                  variant="outline"
                                  size="icon"
                                  className="self-end"
                                  onClick={() => removeVariationOption(variationIndex, optionIndex)}
                                  disabled={variation.options.length <= 1}
                                >
                                  <Trash2 className="h-4 w-4" />
                                </Button>
                              </div>
                            </div>
                          ))}
                        </div>

                        <Button
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => addVariationOption(variationIndex)}
                        >
                          <Plus className="mr-2 h-4 w-4" />
                          Add Option
                        </Button>
                      </CardContent>
                    </Card>
                  ))}

                  <Button type="button" variant="outline" onClick={addVariation}>
                    <Plus className="mr-2 h-4 w-4" />
                    Add Variation
                  </Button>
                </div>
              </CardContent>
              <CardFooter className="flex justify-end gap-2">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => {
                    setIsCreating(false)
                    setIsEditing(false)
                    setCurrentSetId(null)
                  }}
                >
                  Cancel
                </Button>
                <Button type="submit">
                  <Save className="mr-2 h-4 w-4" />
                  {isCreating ? "Create Set" : "Save Changes"}
                </Button>
              </CardFooter>
            </Card>
          </form>
        </Form>
      )}

      {/* Variation Sets List */}
      {!isCreating && !isEditing && (
        <div className="space-y-6">
          {filteredVariationSets.length > 0 ? (
            <div className="grid grid-cols-1 gap-6">
              {filteredVariationSets.map((set) => (
                <Card key={set.id} className="overflow-hidden">
                  <CardHeader className="pb-3">
                    <div className="flex justify-between items-start">
                      <div>
                        <CardTitle>{set.name}</CardTitle>
                        {set.description && <CardDescription>{set.description}</CardDescription>}
                      </div>
                      <div className="flex gap-2">
                        <TooltipProvider>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <Button variant="outline" size="icon" onClick={() => handleEditSet(set.id || "")}>
                                <Edit className="h-4 w-4" />
                              </Button>
                            </TooltipTrigger>
                            <TooltipContent>
                              <p>Edit</p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>

                        <TooltipProvider>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <Button variant="outline" size="icon" onClick={() => handleDuplicateSet(set.id || "")}>
                                <Copy className="h-4 w-4" />
                              </Button>
                            </TooltipTrigger>
                            <TooltipContent>
                              <p>Duplicate</p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>

                        <TooltipProvider>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <Button
                                variant="outline"
                                size="icon"
                                className="text-destructive hover:bg-destructive/10 hover:text-destructive"
                                onClick={() => handleDeleteSet(set.id || "")}
                              >
                                <Trash2 className="h-4 w-4" />
                              </Button>
                            </TooltipTrigger>
                            <TooltipContent>
                              <p>Delete</p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Variation</TableHead>
                          <TableHead>Options</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {set.variations.map((variation) => (
                          <TableRow key={variation.id}>
                            <TableCell className="font-medium">
                              {variation.name}
                              {variation.description && (
                                <p className="text-xs text-muted-foreground mt-1">{variation.description}</p>
                              )}
                            </TableCell>
                            <TableCell>
                              <div className="flex flex-wrap gap-2">
                                {variation.options.map((option) => (
                                  <TooltipProvider key={option.id}>
                                    <Tooltip>
                                      <TooltipTrigger asChild>
                                        <Badge variant="secondary">{option.value}</Badge>
                                      </TooltipTrigger>
                                      {option.description && (
                                        <TooltipContent>
                                          <p>{option.description}</p>
                                        </TooltipContent>
                                      )}
                                    </Tooltip>
                                  </TooltipProvider>
                                ))}
                              </div>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </CardContent>
                  <CardFooter className="bg-muted/50 py-3">
                    <div className="flex items-center text-sm text-muted-foreground">
                      <span className="mr-2">{set.variations.length} variation types</span>
                      <span>•</span>
                      <span className="ml-2">
                        {set.variations.reduce((total, variation) => total + variation.options.length, 0)} total options
                      </span>
                    </div>
                  </CardFooter>
                </Card>
              ))}
            </div>
          ) : (
            <Card className="border-dashed">
              <CardContent className="p-8 flex flex-col items-center justify-center">
                <AlertCircle className="h-12 w-12 text-muted-foreground mb-4" />
                {searchTerm ? (
                  <>
                    <h3 className="text-lg font-medium mb-2">No matching variation sets</h3>
                    <p className="text-muted-foreground mb-6 text-center max-w-md">
                      No variation sets match your search criteria. Try a different search term or create a new set.
                    </p>
                    <Button variant="outline" onClick={() => setSearchTerm("")}>
                      Clear Search
                    </Button>
                  </>
                ) : (
                  <>
                    <h3 className="text-lg font-medium mb-2">No variation sets found</h3>
                    <p className="text-muted-foreground mb-6 text-center max-w-md">
                      Get started by creating your first variation set to use across multiple products.
                    </p>
                    <Button onClick={() => setIsCreating(true)}>
                      <Plus className="mr-2 h-4 w-4" />
                      Create Variation Set
                    </Button>
                  </>
                )}
              </CardContent>
            </Card>
          )}
        </div>
      )}

      {/* Delete Confirmation Dialog */}
      <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you sure?</AlertDialogTitle>
            <AlertDialogDescription>
              This action cannot be undone. This will permanently delete the variation set and remove it from our
              servers.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={confirmDeleteSet}
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
            >
              Delete
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  )
}
